import React from 'react';
import { connect } from 'react-redux';
import { DateRangePicker } from 'react-dates';
import { setTextFilter, sortByDate, sortByAmount, setStartDate, setEndDate } from '../actions/filters';


class ExpenseListFilters extends React.Component { 
    state = {
        calendarFocused: null,
    };
    // dispatch the new values by passing the argument dates.
    onDatesChange = ( { startDate, endDate }) => {
        this.props.dispatch(setStartDate(startDate));
        this.props.dispatch(setEndDate(endDate));
    }
    onFocusChange = (calendarFocused) => {
        this.setState(() => ({ calendarFocused: calendarFocused }))
    }
    render () {
        return (
            <div>
                <input type='text' value={this.props.filters.text} onChange={(e) => {
                    // set Text filter accepts as string as argument
                        // e.target.value is the current value being passed-in
                    props.dispatch(setTextFilter(e.target.value))
                }}
                />
                <select
                // set the filter into sortByAmount or sortByDate
                 value={this.props.filters.sortBy} onChange={(e) => {
                    if (e.target.value === 'date') {
                         this.props.dispatch(sortByDate())
                    }else if (e.target.value === 'amount') {
                         this.props.dispatch(sortByAmount())
                    }
                 }}
                 >
                    <option value="date">Date</option>
                    <option value="amount">Amount</option>
                </select>
                 <DateRangePicker 
                    startDate={this.props.filters.startDate}
                    endDate={this.props.filters.endDate}
                    onDatesChange={this.onDatesChange}
                    focusedInput={this.state.calendarFocused}
                    onFocusChange={this.onFocusChange}
                    showClearDates={true}
                    numberOfMonths={1}
                    isOutsideRange={() => false}
                 />
            </div>
        );
        
    }

}

// input box 

// connects the regular function to the redux store
const mapStateToProps = (state) => {
    return {
        filters: state.filters
    }
}

export default connect(mapStateToProps)(ExpenseListFilters);